import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {
  data: any;
  list_choosed: any = [];
  list_choosed_scroll: any = [];
  list_un_choosed: any = [];
  list_un_choosed_scroll: any = [];
  tabs: any = ['Selected', 'Unselected']
  heightScroll: string;

  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    private vhComponent: VhComponent,
    private cdRef: ChangeDetectorRef

  ) {
    this.data = this.router.getCurrentNavigation().extras.state.data;
  }

  ngOnInit(): void {
    this.getDataProduct(this.data)
  }

  ngAfterViewChecked() {
    if (document.getElementById('detail-category-products') && document.querySelector(".ant-table-thead") && document.querySelector('.detail-category-products-header') && document.querySelector(".ant-table-pagination")) {
      let height = document.getElementById('detail-category-products').clientHeight - document.querySelector(".ant-table-thead").clientHeight - document.querySelector('.detail-category-products-header').clientHeight - 90 + 'px'
      this.heightScroll = height
    }
    this.cdRef.detectChanges();
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/dishes/menu']);
  }
  getDataProduct(category) {
     
    this.list_choosed = this.vhQueryCafe.getlocalProducts().filter(item => {
      if (item.id_category == category._id && item.type == 1) {
        item.status = true;
        return true
      } else return false
    });
    this.list_choosed_scroll = this.renderProduct(this.list_choosed);
    this.list_un_choosed = this.vhQueryCafe.getlocalProducts().filter(item => {
      {
        if (item.id_category == "" && item.type == 1) {
          item.status = false;
          return true
        } else return false
      }
    });
    this.list_un_choosed_scroll = this.renderProduct(this.list_un_choosed);
  }
  renderProduct(products) {
    let data_scroll: any = [];
    let k = 0;
    for (let i = 0; i < products.length; i++) {
      if (!data_scroll[k]) data_scroll[k] = [products[i]]
      else data_scroll[k].push(products[i]);
      if (data_scroll[k].length == 2) k++;
    }
    return data_scroll;
  }
  update(item) {
    this.vhQueryCafe
      .updateProduct(item._id, { id_category: item.id_category == "" ? this.data._id : "" })
      .then(res => {
        if (res) {
          this.vhComponent.showLoading("", "transparent-loading")
            .then(() => this.getDataProduct(this.data))
        }
      })
  }
  indexTab: number = 0;
  selectedIndexChange(event) {
    this.indexTab = event;
  }

  gotoDishesDetail(data) {
    let dataRestore = this.data
    this.router.navigate(['/cafe/dashboard/management/dishes/dish-list/detail'], {
      state: {
        data: data,
        dataRestore,
        backToMenu: true,
      }
    });
  }

    /** hàm sort cho các cột */
    nameCol = false;
    name_categoryCol = false;
    time_warningCol = false;
    /** Hàm thực hiện sắp xếp theo collect
     *
     * @param colName       // tên cột muôn sắp xếp
     */
    sortTable(colName) {
      switch (colName) {
        case 'name':
          if (this.nameCol) {
            this.list_choosed = this.vhAlgorithm.sortStringbyASC(
              [...this.list_choosed],
              colName
            );
          } else {
            this.list_choosed = this.vhAlgorithm.sortStringbyDESC(
              [...this.list_choosed],
              colName
            );
          }
          break;
        case 'name_category':
          if (this.name_categoryCol) {
            this.list_choosed = this.vhAlgorithm.sortStringbyASC(
              [...this.list_choosed],
              colName
            );
          } else {
            this.list_choosed = this.vhAlgorithm.sortStringbyDESC(
              [...this.list_choosed],
              colName
            );
          }
          break;
        case 'time_warning':
          if (this.time_warningCol) {
            this.list_choosed = this.vhAlgorithm.sortNumberbyASC(
              [...this.list_choosed],
              colName
            );
          } else {
            this.list_choosed = this.vhAlgorithm.sortNumberbyDESC(
              [...this.list_choosed],
              colName
            );
          }
          break;
      }
    }
}
